body{
    margin: 0;
    padding: 0;
    display: flex;
    text-align: center;
    justify-content: center;
    font-family:Arial, Helvetica, sans-serif;
    background-color: black;
    color: white;
}
h1{
  animation:anime 2s linear infinite alternate; 
}
h2{
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 6%;
}
@keyframes anime {
  from{
      transform: scale(0.8);
  }
  to{
      transform: scale(1);
  }
}
.txt1{
    text-align: center;
    font-size: 5vmin;
    font-weight: 700;
    animation: netflix_style 3.5s infinite;
    outline: none;
    white-space: nowrap;
  }
  @keyframes netflix_style{
    0% {
      text-shadow: makelongshadow(100,1);
      color:#f3f3f3;
      transform: scale(1.5, 1.5);
    }
    10% {
      text-shadow: makelongshadow(100,1.5);
      color:#f3f3f3;
      transform: scale(1.5, 1.5);
    }
    15% {
       color:#f3f3f3;
    }
    20% {    
      color:#1fe879;
      text-shadow: none;
      transform: scale(1.1, 1.1);
    }
    75% {
      opacity: 1;
    }
    80% {
      opacity: 0;    
      color:#1fe879;
      transform: scale(0.85, 0.9);
    }
    
    100% {
      opacity: 0;
    }
  }
  .txt12{
    text-align: center;
    font-size: 5vmin;
    font-weight: 700;
    animation: netflix_style1 3.5s infinite;
    outline: none;
    white-space: nowrap;
  }
  @keyframes netflix_style1{
    0% {
      text-shadow: makelongshadow(100,1);
      color:#f3f3f3;
      transform: scale(1.5, 1.5);
    }
    10% {
      text-shadow: makelongshadow(100,1.5);
      color:#f3f3f3;
      transform: scale(1.5, 1.5);
    }
    15% {
       color:#f3f3f3;
    }
    20% {    
      color:#e90418;
      text-shadow: none;
      transform: scale(1.1, 1.1);
    }
    75% {
      opacity: 1;
    }
    80% {
      opacity: 0;    
      color:#e90418;
      transform: scale(0.85, 0.9);
    }
    
    100% {
      opacity: 0;
    }
  }
.txt{
    font-size:2.5vw;
    margin:auto;
    margin-top: 10%;
    font-weight: 500;
}
.outer{
    width:100vw;
    color:white;
    display: flex;
    justify-content: space-around;
    margin-bottom: 3%;
}
.bg1{
    background-color: rgb(224, 144, 58);
}
.bg2{
    background-color: rgb(213, 71, 107);
}
.bg3{
    background-color: rgb(61, 69, 209);
}
.bg4{
    background-color: rgb(68, 181, 172);
}
.bg5{
    background-color: rgb(221, 0, 255);
}
.bg6{
    background-color: rgb(247, 121, 138);
}
.card{
    width:25vw;
    height:25vh;
    border-radius: 20px;
    transition: 90ms linear; 
}
.card:hover {
    transform: scale(1.1);
  }
  @media (max-width:768px){
    .outer{
      width:100vw;
      height: 30vh;
    }
  
  }